.cat-info {
  width: 900px;
  gap: 20px;
  margin-top: 30px;
  margin-left: 20px;
  margin-right: auto;
  display: flex;
  box-shadow: 16px 10px 15px -8px rgba(0, 0, 0, .75);
}

.span {
  font-weight: 700;
}

.descr {
  width: 400px;
  font-size: 18px;
}

.breed-select {
  width: 500px;
  margin-top: 15px;
  margin-left: 15px;
  font-size: 24px;
}

.loading {
  margin-top: 50px;
  margin-left: 100px;
  display: none;
}

.loader {
  width: 68px;
  height: 68px;
  box-sizing: border-box;
  border: 3px solid #251c1c;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 56px;
  height: 56px;
  border: 3px solid rgba(0, 0, 0, 0);
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  opacity: 0;
}

